import React from 'react';
import styled from 'styled-components';
//import { graphql } from 'gatsby'
import { Section, Container } from '@components/global';
//import { get } from "lodash";
import ExternalLink from '@common/ExternalLink';

const MediaRelease = () => (
    <Section id="releases" accent="secondary">
      <Container>
        <h3>Press Releases</h3>
        <ReleaseWrapper> 
            <Grid>
            <p><small>Press Release  －  2019. 03. 20</small></p>
            <p><ExternalLink href="google.com">A buddha is flying over the wall.</ExternalLink></p>

            </Grid>
        </ReleaseWrapper>
      </Container>
    </Section>
);



const ReleaseWrapper = styled.div`
  padding: 20px 0 20px 0;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S1000};
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 15px 0;
    margin-left: 0%;
  }
`;


const Grid = styled.div`
  display: grid;
  grid-template-columns: 2fr 5fr;
  grid-gap: 20px;
  text-align: left;
  align-items: center;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 10px;
    grid-gap: 5px;

    &:last-child {
      margin-bottom: 24px;
    }
  }

  p{
    margin: 0;
  }
`;



export default MediaRelease;